import React from "react"
import "../images/depression.svg"
import "../images/healthcare-and-medical.svg"
import "../images/intelligence.svg"
import "../images/no-smoking.svg"
import "../images/thinking (1).svg"
import "../images/thinking.svg"

const help = () => (
    <section className="help-section" id="help">
        <h1>Formy Pomocy</h1>
        <div className="container">
            
            <div className="card">
                <div className="face face1">
                    <div className="content">
                        <img src="depression.svg" width="100px"></img>
                        <h3>KONSULTACJA PSYCHOLOGICZNA</h3>
                    </div>
                </div>
                <div className="face face2">
                    <div className="content">
                        <p>Konsultacja psychologiczna jest psychologicznym wiadczeniem zdrowotnym, ktre dotyczy badania stanu psychicznego pacjenta metodami psychologii klinicznej. Konsultacja psychologiczna jest ocen stanu psychicznego, rozumian jako specjalistyczne postpowanie obejmujce: psychologiczn diagnoz problemow (moe mie ona charakter opisowy bd rnicowy, stanowi diagnoz czstkow lub caociow) oraz sformuowanie wskaza dotyczcych dalszego postpowania z pacjentem. To od jednego do trzech spotka, podczas ktrych zbieram kompleksowy wywiad psychologiczny, okrelam wstpnie rodzaj problemu oraz wraz z pacjentem szukam rnych sposobw jego rozwizania.</p>
                    </div>
                </div>
            </div>
            <div className="card">
                <div className="face face1">
                    <div className="content">
                        <img src="healthcare-and-medical.svg" width="100px"></img>
                        <h3>PORADA PSYCHOLOGICZNA</h3>
                    </div>
                </div>
                <div className="face face2">
                    <div className="content">
                        <p>Porada psychologiczna jest specjalistycznym wiadczeniem zdrowotnym nastawionym  na rozwizanie zgaszanego przez pacjenta, jego rodzin lub opiekuna problemu. Porada obejmuje:</p>
                        <ol>
                            <li>wstpn rozmow , otwierajc kontakt,</li>
                            <li>wywiad i obserwacje,</li>
                            <li>okrelenie problemu,</li>
                            <li>zakoczenie kontaktu poprzez przekazanie pacjentowi obserwacji i wyjanie lub/i skierowanie go do odpowiedniej dla niego formy leczenia.</li>
                        </ol>
                        <p>Porada moe obejmowa pomoc psychologiczn i/lub dodatkowe kontrolne badania psychologiczne.</p>
                        <p>Specyficzn form porady jest wizyta psychologa w domu pacjenta.</p>
                    </div>
                </div>
            </div>
            <div className="card">
                <div className="face face1">
                    <div className="content">
                        <img src="intelligence.svg" width="100px"></img>
                        <h3>PSYCHOLOGICZNA INTERWENCJA W KRYZYSIE</h3>
                    </div>
                </div>
                <div className="face face2">
                    <div className="content">
                        <p>Specjalistyczne wiadczenie zdrowotne polegajce na udzielaniu  pomocy  w psychologicznym kryzysie wywoanym chorob lub innymi okolicznociami. 
W stanie ostrego kryzysu potrzebna jest natychmiastowa pomoc trwajca a do ustpienia objaww. To forma pomocy, ktra skierowana jest do tych 
z Pastwa, ktrzy borykaj si z problemami nagymi. Interwencja w kryzysie to forma pomocy, ktra ma na celu przywrcenie rwnowagi, poczucia bezpieczestwa, odzyskanie umiejtnoci radzenia sobie i obnienie lkw. </p>
                    </div>
                </div>
            </div>
            <div className="card">
                <div className="face face1">
                    <div className="content">
                        <img  src="thinking (1).svg" width="100px"></img>
                        <h3>TERAPIA PSYCHOLOGICZNA</h3>
                    </div>
                </div>
                <div className="face face2">
                    <div className="content">
                        <p>Terapia psychologiczna jest specjalistycznym wiadczeniem zdrowotnym udzielanym przez psychologa pacjentowi i/lub jego rodzinie, czy te opiekunowi. Celem terapii psychologicznej jest osignicie przez pacjenta optymalnego poziomu funkcjonowania. Korzystajc z terapii pacjent ma moliwo poznania i zrozumienia siebie i swoich zachowa w rnych sytuacjach. W trakcie terapii uzyskiwany bdzie wgld i zrozumienie rda problemu/trudnoci. Terapi psychologiczn zwykle winna poprzedza porada psychologiczna i/lub konsultacja psychologiczna. Terapia psychologiczna jest podstawow, zwyczajowo pierwsz form pomocy psychologicznej pacjentowi i/lub jego rodzinie/opiekunowi.</p>
                    </div>
                </div>
            </div>
            <div className="card">
                <div className="face face1">
                    <div className="content">
                        <img src="no-smoking.svg" width="100px"></img>
                        <h3>TERAPIA UZALENIE</h3>
                    </div>
                </div>
                <div className="face face2">
                    <div className="content">
                        <p>Terapia uzalenie to proces majcy prowadzi do zaprzestania lub zmniejszenia czstotliwoci przyjmowania substancji psychoaktywnych. Terapia uzalenie  behawioralnych to proces majcy prowadzi  do zaprzestania lub zmniejszenia zachowa problemowych.</p>
                    </div>
                </div>
            </div>
            <div className="card">
                <div className="face face1">
                    <div className="content">
                        <img src="thinking.svg" width="100px"></img>
                        <h3>DIAGNOZA PSYCHOLOGICZNA</h3>
                    </div>
                </div>
                <div className="face face2">
                    <div className="content">
                        <p>Diagnoza w psychologii polega na opisie pozytywnych i negatywnych waciwoci psychicznych oraz psychologicznych mechanizmw funkcjonowania jednostki, wyjanianiu problemw w funkcjonowaniu tej jednostki oraz prognozie skutkw zachowa i zjawisk i moliwoci modyfikowania tego co problematyczne w funkcjonowaniu. Ma na celu ustalenie mechanizmw psychologicznych, przyczyn oraz stopnia nieprawidowoci w rnych sytuacjach yciowych.W diagnozie psychologicznej posuguj si obserwacj, wywiadem oraz testami psychologicznymi atestowanymi przez Polskie Towarzystwo Psychologicznego. Wykonuj nastpujce badania: badania osobowoci, badania ilorazu inteligencji, badania neuropsychologiczna.</p>
                    </div>
                </div>
            </div>
            
        </div>
    </section>
)
    export default help