import React from "react"
import "../styles/layout.scss"
import "../images/PaniOla.png"
import "../images/education.svg"
import "../images/education (1).svg"

const aboutMe = () => (
<section className="section-about-me" id="about-me">
    <div className="container-about-me">
        <div className="image-about-me">
            <img className="photo-me" src="PaniOla.png"></img>
            <div className="card">
            </div>
        </div>
        <div className="flex-box">
        <div className="text-about-me">
            <h2>Kilka sw o mnie</h2>
            <p>Jestem psychologiem o specjalnoci psychologia kliniczna dziecka, psychologia sdowa, specjalist psychologii transportu nr ewidencyjny  029/08F, certyfikowanym specjalist psychoterapii uzalenie nr SP/1339/2017, w trakcie studiw podyplomowych ze specjalizacji z psychologii klinicznej. 
                Studia psychologiczne ukoczyam w 2002 roku na Uniwersytecie im. Adama Mickiewicza w Poznaniu. W 2008 roku ukoczyam studnia podyplomowe na Uniwersytecie Warszawskim z psychologii transportu. Jestem czonkiem Stowarzyszenia Psychologw Sdowych w Polsce. Stale podnosz swoje kwalifikacje uczestniczc w kursach, szkoleniach i konferencjach obejmujcych midzy innymi tematyk psychoterapii systemowej indywidualnej i rodzin, przemocy seksualnej wobec dzieci, autodestrukcyjnych schematw mylenia u osb uzalenionych i wspuzalenionych,  pomocy psychologicznej dla rodzin modziey z problemami uzalenie, interwencji wychowawczej wobec ucznia dysfunkcyjnego, agresji -  budowania systemw bezpieczestwa. Dowiadczenie zawodowe zdobywaam w pracujc w Szpitalu Psychiatrycznym dla Dzieci i Modziey w Zaborze, oraz w  szkolnictwie pracujc z dziemi, modzie oraz ich rodzinami jak rwnie subach mundurowych. Ponadto odbyam liczne praktyki w placwkach suby zdrowia oraz opiekuczych. Od wielu lat pracuj jako psycholog sdowy z modzie i osobami dorosymi zagroonymi wykluczeniem spoecznym. Od 2004 roku do 2019r. byam bieg sdow w zakresie psychologii  z rekomendacj Prokuratury Okrgowej w Zielonej Grze. W swojej pracy ceni sobie profesjonalizm i trosk o dobro pacjenta. Kieruj si najwyszymi standardami etyki zawodowej - przestrzegam zasad Kodeksu Etyczno - Zawodowego Psychologa Polskiego Towarzystwa Psychologicznego, za swoj prac poddaj systematycznej superwizji. Pracuj metod dialogu motywujcego, oraz na zaoeniach terapii skoncentrowanej na rozwizaniach. 
            </p>
        </div>
        <div className="courses">
            <h2>Ukoczone szkolenia</h2>
            <div className="line-course">
                <h3>Wielkopolskie Towarzystwo Terapii Systemowej  ukoczone szkolenie podstawowe 2003/2004r.</h3>
                <img width="75px" src="education.svg"></img>
            </div>
            <div className="line-course-reverse">
                <h3>Polski Instytut Psychoterapii Krtkoterminowej  Diagnoza kliniczna DSM-IV 2001r.</h3>
                <img width="75px" src="education.svg"></img>
            </div>
            <div className="line-course">
                <h3>Centrum Psychoterapii Poznawczo-Behawioralnej  Zesp Nadpobudliwoci Psychoruchowej)- 2005r. </h3>
                <img width="75px" src="education.svg"></img>
            </div>
            <div className="line-course-reverse">
                <h3>Towarszystwo Rozwoju Rodziny- ukoczone szkolenie w zakresie profilaktyki i terapii uzalenie behawioralnych 2014r. </h3>
                <img width="75px" src="education.svg"></img>
            </div>
            <div className="line-course">
                <h3>Karkonoskie Towarzystwo Psychoprofilaktyczne  ukoczone szkolenie z terapii osb uzalenionych od pornogafii, seksu, jako odpowied na konsekwencje rozwojowe i zdrowotne u pacjentw Szklarska Porba 2015r. </h3>
                <img width="75px" src="education.svg"></img>
            </div>
        </div>
        </div>
        <div className="lectures">
            <h2>Odbyte konferencje</h2>
            <div className="line-course">
                <h3>Midzynarodowa Konferencja Naukowo-Szkoleniowa Przemoc seksualna- perspektywa ofiar i sprawcw-  Pozna 2016r.</h3>
                <img width="100px" src="education (1).svg"></img>
            </div>
            <div className="line-course-reverse">
                <h3>Polskie Towarzytwo Psychiatryczne Psychiatria-Moliwoci Rozwoju  Gniezno 2002r.</h3>
                <img width="100px" src="education (1).svg"></img>
            </div>
                <div className="line-course"><h3>Polskie Towarzystwo Balintowskie Komunikacja interpersonalna w procesie pomagania  Zielona Gra 2002r.</h3>
                <img width="100px" src="education (1).svg"></img>
            </div>
            <div className="line-course-reverse">
                <h3>Uniwersytet Zielonogrski Zrozumie dziecko z FASD- Zielona Gra 2018r. </h3>
                <img width="100px" src="education (1).svg"></img>
            </div>
            <div className="line-course">
                <h3>Biuro Profialktyki i Przeciwdziaania Uzalenieniom Miejskiego Orodka Pomocy Spoecznej w Zielonej Grze  Alkohol a przemc w rodzinie- Zielona Gra 2019r. </h3>
                <img width="100px" src="education (1).svg"></img>
            </div>
            <div className="line-course-reverse">
                <h3>Fundacja Dziecko w Centrum  wsparcie osb pokrzywdzonych przestpstwe- perspektywa prawna, psychologiczna i spoeczna -Pozna 2020r.</h3>
                <img width="100px" src="education (1).svg"></img>
            </div>
            <div className="line-course">
                <h3>Sekcja Psychologii Sdowej  7 konferencja psychologii sdowej  Krakw 2014r.</h3>
                <img width="100px" src="education (1).svg"></img>
            </div>
            <div className="line-course-reverse">
                <h3>Uniwersytet im. Adama Mickiewicza  III Krajowa konferencja z psycholgii klinicznej  Pozna 2018r.</h3>
                <img width="100px" src="education (1).svg"></img>
            </div>
            <div className="line-course">
                <h3>Klinika Psychiatrii Dorosych Uniwersytet Medyczny  Neuropsychiatria i Neuroposychologia -Pozna 2019r.</h3>
                <img width="100px" src="education (1).svg"></img>
            </div>
        </div>
    </div>
    <div className="element1"/>
    <div className="element2"/>
</section>
)
    export default aboutMe