import React from "react"
import "../images/social-care.svg"
import "../images/kid-and-baby.svg"
import "../images/support.svg"
import "../images/Awesome Human Brain Hemispheres 1.svg"

const offer = () => (
    <section className="offer-section" id="offer">
        <h1>Oferta</h1>
        <div className="container"><div className="cards">
            <div className="card">
                <div className="face face1">
                    <div className="content">
                        <img width="100px" src="social-care.svg"></img>
                        <h3>Pomoc dorosym i modziey</h3>
                    </div>
                </div>
                <div className="face face2">
                    <div className="content">
                        <ul>
                            <li>uzalenie, w tym behawioralnych,</li>
                            <li>wspuzalenienia</li>
                            <li>radzenia sobie z depresj, lkami</li>
                            <li>w trudnociach rozwojowych i kryzysach emocjonalnych,</li>
                            <li>w trudnociach w kontaktach spoecznych (relacji z rwienikami).</li>
                        </ul>
                    </div>
                </div>
            </div>
            <div className="card">
                <div className="face face1">
                    <div className="content">
                        <img width="100px" src="kid-and-baby.svg"></img>
                        <h3>Pomoc dzieciom</h3>
                    </div>
                </div>
                <div className="face face2">
                    <div className="content">
                        <ul>
                            <li>w sytuacjach trudnych, stresujcych (konflikty szkolne, rodzinne, rozwd rodzicw, straty, adaptacja)</li>
                            <li>w depresjach i lkach</li>
                            <li>w zaburzeniach zachowania, zaburzeniach jedzenia</li>
                        </ul>
                    </div>
                </div>
            </div>
            <div className="card">
                <div className="face face1">
                    <div className="content">
                        <img width="100px" src="support.svg"></img>
                        <h3>Pomoc tym ktrzy</h3>
                    </div>
                </div>
                <div className="face face2">
                    <div className="content">
                        <ul>
                            <li>maj trudnoci i nie wiedz jak sobie z nimi poradzi  pragn zmiany w sobie i swoim yciu</li>
                            <li>szukaj rwnowagi, harmonii i satysfakcji w yciu prywatnym i zawodowym</li>
                            <li>czuj si samotni i niezrozumiali</li>
                            <li>cierpi na choroby psychosomatyczne</li>
                            <li>stoj w obliczu straty, rozstania, konfliktu, powanej decyzji yciowej</li>
                        </ul>
                    </div>
                </div>
            </div>
            </div><div className="offer-image">
            <img src="Awesome Human Brain Hemispheres 1.svg" ></img></div>
        </div>
    </section>
)
    export default offer